	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}

.bg-gradual-blue {
	background-image: linear-gradient(45deg, #0081ff, #1cbbb4);
	color: #ffffff;
}
.cu-bar .content {
	position: absolute;
